#demo{
    word-break: break-all;
}
#div1 {
    position: fixed;
    left: 50%;
    transform: translateX(-50%);
   
    top: 20px;
}

#div1::before {
    content: '';
    display: block;
   
    position:absolute;
}

#div1::after {
        content: '';
        display: block;
       
        position: absolute;
    }
    @media (max-width:500px){
        #html{
            height: 60vh;
            overflow: auto;
        }
        #div1Wrapper {
            height: 40vh;
            
        }
        #div1{
            position: relative;
            top:0;
            right:0;

        }
    }